import React, { useEffect } from "react"
import './Home.css'
import 'bootstrap/dist/css/bootstrap.css'
import Image from './Images/poto1.jpg'
import Image2 from './Images/poto2.jpg'
import Image3 from './Images/poto3.jpg'
import { useState } from 'react'
import Popular from "./Popular"

export default function Home() {
  const [images, setImages] = useState([Image, Image2, Image3])
  const [currentImage, setCurrentImage] = useState(images[0])
  useEffect(()=>{
    let y=0;
    let x=setInterval(()=>{
      if(y===2){
        y=0;
      }else{
        y++
      }
      setCurrentImage(images[y])
    },3000)
 })
  return (
    <div className='home'>
      <img className='slider' src={currentImage} alt="Restaurant Image" />
      <Popular />
    </div>
  )
}